<template>
    <div>
        <div>
          <input
            type="text"
            class="input-search"
            placeholder="Search Input"
            v-model="searchInputValue"
            @input="getSearchResult"
          />
          <p v-if="isLoading">Loading....</p>
          <p v-if="!serverError && apiSearchResult.length === 0 && searchInputValue !== '' && !isLoading">No result.</p>
          <ul v-if="apiSearchResult">
            <li v-for="searchResult in apiSearchResult" :key="searchResult.id" @click="getCity(searchResult)">
              {{ searchResult.place_name }}
            </li>
          </ul>
          <p v-if="isError">Opps....</p>
        </div>
        <div>
            <div v-for="city in cities" :key="city.id">
                <p>City: {{ city.city }}, {{ city.state }}</p>
                <p>Geo<br/> lat : {{ city.geo.lat }} <br/> lng: {{ city.geo.lng }}</p>
            </div>
        </div>
    </div>
  </template>
  
  <script setup>
  import { ref } from "vue";
  import axios from "axios";
  
  const searchInputValue = ref("");
  const searchTimeout = ref(null);
  const apiSearchResult = ref([]);
  const isError = ref(false);
  const isLoading = ref(false);
  const ApiKey =
    "pk.eyJ1Ijoiby10c3lrdW5vdiIsImEiOiJjbGVhZzkyODIwZzA0M25uMjRqZnYxNWxxIn0.vY-9LdslsvtCt-VPag3TaA";

    //
const cities = ref([])
    
  
  const getSearchResult = () => {
    isLoading.value = true;
    clearTimeout(searchTimeout.value);
    searchTimeout.value = setTimeout(async () => {
      if (searchInputValue.value !== "") {
        try {
          const res = await axios.get(
            `https://api.mapbox.com/geocoding/v5/mapbox.places/${searchInputValue.value}.json?access_token=${ApiKey}&types=place`
          );
          apiSearchResult.value = res.data.features;
          isLoading.value = false;
          console.log(apiSearchResult);
        } catch {
          isError.value = true;
          isLoading.value = false;
        }
  
        return;
      }
      isLoading.value = false;
      apiSearchResult.value = [];
    }, 250);
  };

  //

  const getCity = (searchResult) => {
    const [city, state] = searchResult.place_name.split(',')
    const cityInfo = {
        id: searchResult.id,
        city: city,
        state: state,
        geo: {
            lat: searchResult.geometry.coordinates[1],
            lng: searchResult.geometry.coordinates[0],
        },
        preview: true
        
    }
    cities.value.push(cityInfo)
    isLoading.value = false;
      apiSearchResult.value = [];
      searchInputValue.value= ''
  }
  </script>
  
  <style scoped>
  .input-search {
    border: none;
    outline: none;
    background-color: transparent;
    color: #e7f5dc;
    border-bottom: solid 1px #c0dfc2;
  }
  .input-search:focus {
    border-bottom: solid 1px #4b6154;
    box-shadow: 0px 18px 14px -3px rgba(75, 97, 84, 0.81);
  }
  </style>
  